import React, { useState, useReducer } from "react";
import styled from "styled-components";
import RemoveRedEyeOutlinedIcon from "@mui/icons-material/RemoveRedEyeOutlined";
import VisibilityOffOutlinedIcon from "@mui/icons-material/VisibilityOffOutlined";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import FolderSpecialIcon from "@mui/icons-material/FolderSpecial";
import LoopIcon from "@mui/icons-material/Loop";

const MenuItemsContainer = styled.div`
  width: 150px;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
`;

const Title = styled.div`
  width: 100%;
  padding: 15px 10px;
  font-size: 12px;
  display: flex;
  justify-content: space-around;
`;

const LeftSideMenu = styled.div`
  width: 100%;
  border-right: 1px solid #000;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  button {
    margin-bottom: 20px;

    &:last-child {
      margin: 0;
    }
  }
`;

const RightSideMenu = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  button {
    margin-bottom: 20px;

    &:last-child {
      margin: 0;
    }
  }
`;

const MenuContent = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  padding: 15px 5px;
`;

const Tile = styled.div`
  width: 40px;
  height: 40px;
  background: ${({ color }) => color};
  border: ${({ active }) => (active ? `3px solid #fff` : `none`)};
`;

const Circle = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: ${({ color }) => color};
  border: ${({ active }) => (active ? `3px solid #fff` : `none`)};
`;

const VisibilityControl = styled.div`
  width: 90%;
  height: 40px;
  border-top: 1px solid #000;
  display: flex;
  justify-content: space-around;
`;

const FavoriteControl = styled.div`
  width: 90%;
  height: 40px;
`;

const SaveControl = styled.div`
  width: 90%;
  height: 60px;

  div {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  span {
    font-size: 14px;
  }
`;

const Spinner = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  span {
    font-size: 14px;
  }

  .spinning {
    animation: loading 1s linear infinite;
  }

  @keyframes loading {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
`;

const Button = styled.button`
  background: none;
  border: none;
  box-shadow: none;
`;

const SelectButton = styled.button`
  background: none;
  border: none;
  box-shadow: none;
  padding: 0;
`;

const initialFloorState = {
  green: false,
  yellow: false,
  red: false,
  blue: false
};

const initialCushionState = {
  brown: false,
  olive: false,
  lightgreen: false,
  lightblue: false
};

const CustomItemsManager = ({ handleFloorColor, handleInfoBoxVisibility }) => {
  const [favorite, setFavorite] = useState(false);
  const [floor, setFloor] = useState(true);
  const [cushions, setCushions] = useState(true);
  const [saving, setSaving] = useState(false);

  const [floorState, dispatchFloor] = useReducer(
    handleFloorState,
    initialFloorState
  );
  const [cushionState, dispatchCushion] = useReducer(
    handleCushionState,
    initialCushionState
  );

  function handleFloorState(state, action) {
    switch (action) {
      case "yellowOn":
        if (floor) {
          handleFloorColor("yellow");
          handleInfoBoxVisibility({ active: true, content: "piso amarelo" });
        }
        return {
          green: false,
          yellow: true,
          red: false,
          blue: false
        };
      case "greenOn":
        if (floor) {
          handleFloorColor("green");
          handleInfoBoxVisibility({ active: true, content: "piso verde" });
        }
        return {
          green: true,
          yellow: false,
          red: false,
          blue: false
        };
      case "redOn":
        if (floor) {
          handleFloorColor("red");
          handleInfoBoxVisibility({ active: true, content: "piso vermelho" });
        }
        return {
          green: false,
          yellow: false,
          red: true,
          blue: false
        };
      case "blueOn":
        if (floor) {
          handleFloorColor("blue");
          handleInfoBoxVisibility({ active: true, content: "piso azul" });
        }
        return {
          green: false,
          yellow: false,
          red: false,
          blue: true
        };
      default:
        return {
          green: false,
          yellow: false,
          red: false,
          blue: false
        };
    }
  }

  function handleCushionState(state, action) {
    switch (action) {
      case "brownOn":
        return {
          brown: true,
          olive: false,
          lightgreen: false,
          lightblue: false
        };
      case "oliveOn":
        return {
          brown: false,
          olive: true,
          lightgreen: false,
          lightblue: false
        };
      case "lightgreenOn":
        return {
          brown: false,
          olive: false,
          lightgreen: true,
          lightblue: false
        };
      case "lightblueOn":
        return {
          brown: false,
          olive: false,
          lightgreen: false,
          lightblue: true
        };
      default:
        return {
          brown: false,
          olive: false,
          lightgreen: false,
          lightblue: false
        };
    }
  }

  function handleFavorite() {
    setFavorite((prev) => !prev);
  }

  function handleFloor() {
    const floorColor = Object.entries(floorState).find(
      (item) => item[1] === true
    );

    function getCachedInfo() {
      switch (floorColor[0]) {
        case "yellow":
          return "Piso amarelo";
        case "green":
          return "Piso verde";
        case "red":
          return "Piso vermelho";
        case "blue":
          return "Piso azul";
        default:
          return "";
      }
    }

    const cachedInfo = getCachedInfo();

    if (floor) {
      handleFloorColor("");
      handleInfoBoxVisibility({ active: false, content: "" });
    } else {
      handleFloorColor(floorColor[0]);
      handleInfoBoxVisibility({ active: true, content: cachedInfo });
    }
    setFloor((prev) => !prev);
  }

  function handleCushions() {
    setCushions((prev) => !prev);
  }

  function handleSave() {
    setSaving(true);
    setTimeout(() => {
      setSaving(false);
    }, 1000);
  }

  const { green, yellow, red, blue } = floorState;
  const { brown, olive, lightgreen, lightblue } = cushionState;

  return (
    <MenuItemsContainer>
      <Title>
        <div>PISO</div>
        <div>ESTOFADO</div>
      </Title>
      <MenuContent>
        <LeftSideMenu>
          <SelectButton onClick={() => dispatchFloor("yellowOn")}>
            <Tile color="yellow" active={yellow} />
          </SelectButton>
          <SelectButton onClick={() => dispatchFloor("greenOn")}>
            <Tile color="green" active={green} />
          </SelectButton>
          <SelectButton onClick={() => dispatchFloor("blueOn")}>
            <Tile color="blue" active={blue} />
          </SelectButton>
          <SelectButton onClick={() => dispatchFloor("redOn")}>
            <Tile color="red" active={red} />
          </SelectButton>
        </LeftSideMenu>
        <RightSideMenu>
          <SelectButton onClick={() => dispatchCushion("brownOn")}>
            <Circle color="brown" active={brown} />
          </SelectButton>
          <SelectButton onClick={() => dispatchCushion("oliveOn")}>
            <Circle color="olive" active={olive} />
          </SelectButton>
          <SelectButton onClick={() => dispatchCushion("lightgreenOn")}>
            <Circle color="lightgreen" active={lightgreen} />
          </SelectButton>
          <SelectButton onClick={() => dispatchCushion("lightblueOn")}>
            <Circle color="lightblue" active={lightblue} />
          </SelectButton>
        </RightSideMenu>
      </MenuContent>
      <VisibilityControl>
        <Button onClick={handleFloor}>
          {floor ? (
            <RemoveRedEyeOutlinedIcon sx={{ fontSize: 30 }} />
          ) : (
            <VisibilityOffOutlinedIcon sx={{ fontSize: 30 }} />
          )}
        </Button>
        <Button onClick={handleCushions}>
          {cushions ? (
            <RemoveRedEyeOutlinedIcon sx={{ fontSize: 30 }} />
          ) : (
            <VisibilityOffOutlinedIcon sx={{ fontSize: 30 }} />
          )}
        </Button>
      </VisibilityControl>
      <FavoriteControl>
        <Button onClick={handleFavorite}>
          {favorite ? (
            <FavoriteIcon sx={{ fontSize: 30 }} />
          ) : (
            <FavoriteBorderIcon sx={{ fontSize: 30 }} />
          )}
        </Button>
      </FavoriteControl>
      <SaveControl>
        {saving ? (
          <Spinner>
            <div>
              <div className="spinning">
                <LoopIcon sx={{ fontSize: 30 }} />
              </div>
              <span>salvando...</span>
            </div>
          </Spinner>
        ) : (
          <Button onClick={handleSave}>
            <div>
              <FolderSpecialIcon sx={{ fontSize: 50 }} />
              <span>salvar</span>
            </div>
          </Button>
        )}
      </SaveControl>
    </MenuItemsContainer>
  );
};

export default CustomItemsManager;
