import React, { useState } from "react";
import styled from "styled-components";
import CloseIcon from "@mui/icons-material/Close";
import MenuIcon from "@mui/icons-material/Menu";

import CustomItemsManager from "./CustomItemsManager";

const SideBarContainer = styled.div`
  width: 150px;
  height: 100%;
  position: absolute;
  right: 0;
`;

const SideBarContent = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  right: ${({ open }) => (open ? 0 : "-150px")};
  top: 0;
  background: rgba(255, 255, 255, 0.5);
  transition: all 0.5s ease;
`;

const Button = styled.button`
  background: none;
  border: none;
  box-shadow: none;
`;

const CloseButton = styled.div`
  position: absolute;
  width: 100%;
  bottom: 10px;
`;

const MenuButton = styled.div`
  position: absolute;
  width: 100%;
  top: 10px;
  top: ${({ open }) => (open ? "-50px" : "10px")};
  transition: all 0.5s ease;
`;

const Sidebar = ({ handleFloorColor, handleInfoBoxVisibility }) => {
  const [open, setOpen] = useState(true);

  function handleOnClick() {
    setOpen((prev) => !prev);
    handleInfoBoxVisibility({ active: false, content: "" });
  }

  return (
    <SideBarContainer>
      <MenuButton open={open}>
        <Button onClick={handleOnClick}>
          <MenuIcon sx={{ fontSize: 40 }} />
        </Button>
      </MenuButton>

      <SideBarContent open={open}>
        <CustomItemsManager
          handleFloorColor={handleFloorColor}
          handleInfoBoxVisibility={handleInfoBoxVisibility}
        />
        <CloseButton>
          <Button onClick={handleOnClick}>
            <CloseIcon sx={{ fontSize: 40 }} />
          </Button>
        </CloseButton>
      </SideBarContent>
    </SideBarContainer>
  );
};

export default Sidebar;
