import React, { useState, useEffect } from "react";
import styled from "styled-components";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import CloseIcon from "@mui/icons-material/Close";

const InfoBoxWrapper = styled.div`
  position: absolute;
  /* top: 485px; */
  left: 75px;
  bottom: ${({ active, close }) => {
    return active && !close ? `30px` : `-140px`;
  }};
  width: 700px;
  height: 100px;
  transition: all 0.7s ease;
  /* opacity: ${({ active }) => (active ? `1.0` : `0`)}; */
  background: rgba(255, 255, 255, 0.5);
`;

const InfoIconHolder = styled.div`
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  top: -15px;
  left: -15px;
  background: #fff;
`;

const InfoBoxTextFloor = styled.div`
  position: absolute;
  top: -10px;
  left: 0;
  padding: 20px 20px;
  width: 350px;
  height: 100px;
  display: flex;
  justify-content: flex-start;
`;

const CloseButton = styled.div`
  position: absolute;
  top: 55px;
  width: 40px;
  height: 100%;
  right: 10px;
`;

const Button = styled.button`
  background: none;
  border: none;
  box-shadow: none;
`;

const InfoBox = ({ active, content }) => {
  const [close, setClose] = useState(true);

  function handleOnClose() {
    setClose(true);
  }

  useEffect(() => {
    if (active) {
      setClose((prev) => false);
    }
  }, [active, content]);

  return (
    <InfoBoxWrapper active={active} close={close}>
      <InfoIconHolder>
        <InfoOutlinedIcon sx={{ fontSize: 40 }} />
      </InfoIconHolder>
      <InfoBoxTextFloor>
        <p>{content}</p>
      </InfoBoxTextFloor>
      <CloseButton>
        <Button onClick={handleOnClose}>
          <CloseIcon sx={{ fontSize: 40 }} />
        </Button>
      </CloseButton>
    </InfoBoxWrapper>
  );
};

export default InfoBox;
