import React, { useState } from "react";
import Sidebar from "./Sidebar";
import ViewPort from "./ViewPort";
import Screen from "./Screen";
import Floor from "./Floor";
import InfoBox from "./InfoBox";

const Home = () => {
  const [floorColor, setFloorColor] = useState(undefined);
  const [infoBox, setInfoBox] = useState({ active: false, content: "" });

  function handleFloorColor(color) {
    setFloorColor(color);
  }

  function handleInfoBoxVisibility({ active, content }) {
    console.log("home active: ", active);
    setInfoBox({ active, content });
  }

  return (
    <Screen>
      <ViewPort>
        <Sidebar
          handleFloorColor={handleFloorColor}
          handleInfoBoxVisibility={handleInfoBoxVisibility}
        />
        <Floor color={floorColor} />
        <InfoBox active={infoBox.active} content={infoBox.content} />
      </ViewPort>
    </Screen>
  );
};

export default Home;
